import { Component, OnInit, Injector, HostListener } from '@angular/core';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";

import { ApartmentService } from '../../../../api/services/apartment.service';
import { AccountsService } from '../../../.././api/services/accounts.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { ModalService } from '../../../../shared/services/modal.service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-gl-expense',
  templateUrl: './gl-expense.component.html',
  styleUrls: ['./gl-expense.component.scss']
})
export class GlExpenseComponent implements OnInit {

  glAccountsDataList:any;
  glAccountData:string = "";

  glGroupsDataList:any;

  glBankDataList:any;

  glaccountTypeId:any = 166 // for Income
  glAccountIndicator:any = "Expenses"

  isDataLoaded:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 100;
  totalFilterItems:any;

  unitFieldType:string = "unitno";
  unitOrder:boolean = false;

  modalService:any;

  constructor(
  	private injector: Injector,
    public dialog: MatDialog,
  	private apartmentService:ApartmentService,
  	private accountsService:AccountsService,
  	private lookupService: LookupService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { 
  	this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }

  getGroup(id){
     
    var data = _.filter( this.glGroupsDataList, function( item ) {
      if(item.glgroupId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name;
    }

  }

  getBank(id){
     
    var data = _.filter( this.glBankDataList, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase();
    }

  }

  getDate(date){
    return moment(date).format("DD/MM/YYYY");
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  isItemsAvailable(){
  	return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
  	return this.totalItems == 0 ? true : false;
  }

  stopPropagation(event: Event){
    event.stopPropagation();
  }

  getAccounts(){

    this.accountsService.getAllGLAccounts().subscribe((res:any) => {
      

      this.glAccountsDataList = res.filter(item => {
        return item.isActive && parseInt(this.cookieService.get('apartmentId')) && item.indicator == this.glAccountIndicator;
      });

      this.isDataLoaded = true;

      this.totalItems = this.glAccountsDataList.length;

      if(this.totalItems>this.itemLimit){
        this.ItemEndIndex = this.itemLimit;
      }
      else {
        this.ItemEndIndex = this.totalItems;
      }

      this.sharedService.setGlAccountAdded(false);

    });

  }

  ngOnInit() {

  	this.getAccounts();

    this.accountsService.getAllGLGroups().subscribe((res:any) => {
      
      this.glGroupsDataList = res.filter(item => {
        return item.isActive
      });

      
    });

    //bank details
    this.lookupService.getLookupValueByLookupTypeId(27).subscribe((res:any) => {
          
       this.glBankDataList = res;
    },
    error => {
      
    }
    );


    this.sharedService.glaccountaddedcast.subscribe(accountAdded => {

      if(accountAdded){
        this.getAccounts();
      }

    })


    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe(id => {


      if(id != null){

        var params = {
          glAccountId: id,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }

        this.isDataLoaded = false;

        this.accountsService.deleteGLGroup(params).subscribe((res:any) => {

          this.getAccounts();
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    });


  }

}
